<template>
  <div class="header-block">
    <parallax :fixed="false" class="position-absolute">
      <img src="../assets/images/mpc.jpg" alt="gdgdg">
    </parallax>
    <div class="container-fluid height_full dark_background" slot="loading">
      <div class="row align-items-center height_full">
        <div class="col">
					<div class="myself">
						<!-- <img src="../assets/images/userPhoto.jpg" alt="it's me"> -->
        	</div>
          <h1>{{ header }}</h1>
          <p>{{ desc }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Parallax from 'vue-parallaxy'
export default {
  components: {
    Parallax
  },
  name: 'Aboute',
  data () {
    return {
      header: 'Я - Евгений Лукин',
      desc: 'seo-специалист, копирайтер'
    }
  }
}

</script>



