<template>
  <div class="home">
    <Aboute />
    <HomePage />
    <Carousel />
  </div>
</template>

<script>
import Aboute from './Aboute'
import HomePage from './HomePage'
import Carousel from './Carousel'
import Vue from 'vue'
import VueCarousel from 'vue-carousel'
Vue.use(VueCarousel)

export default {
  name: 'App',
  components: {
    Aboute,
    HomePage,
    Carousel
  }
}
</script>



